<template>
  <div class="showContent">
    <requestAndHisSubMenu :routekey="routekey"></requestAndHisSubMenu>
    <a-table
      :columns="columns"
      :dataSource="dataSource"
      :pagination="pagination"
      :loading="loading"
      @change="handleTableChange"
      class="newTable"
    >
      <template slot="created_at" slot-scope="created_at">{{getTime(created_at)}}</template>
      <template slot="response" slot-scope="response">
        <span>{{response.data.label?response.data.label:'--'}}</span>
      </template>
      <template slot="request" slot-scope="request,record">
        <div v-if="record.status=='success'">
          <div class="ocrImgCell">
          <img :src="getImage(request.image_file)">
          </div>
        </div>
        <div v-else>--</div>
      </template>
      <template slot="status" slot-scope="text">
          <span class="statusSpan">
            <span :class="text=='success'?'greenDot':'redDot'">
              <i class="iconfont amd-dot"></i>
            </span>
            {{text}}
          </span>
        </template>
      <template slot="operation" slot-scope="text,record,index">
        <!-- <a-icon v-show="record.status=='success'" @click="() => selectHistory(index)" type="right"/> -->
        <span v-if="record.status=='success'" @click="selectHistory(record)" class="historyDetail"><i class="iconfont amd-arrows_right"></i></span>
      </template>
    </a-table>
  </div>
</template>
<script>
import requestAndHisSubMenu from "@/components/requestAndHisSubMenu";
import { mapGetters } from "vuex";
export default {
  data() {
    const columns = [
      {
        title: "Face Comparison",
        dataIndex: "request",
        align: "center",
        scopedSlots: { customRender: "request" }
      },
      {
        title: "Category",
        dataIndex: "response",
        align: "center",
        scopedSlots: { customRender: "response" }
      },
      {
        title: "Upload Time",
        dataIndex: "created_at",
        align: "center",
        scopedSlots: { customRender: "created_at" }
      },
      {
        title: "Status",
        dataIndex: "status",
        align: "center",
        scopedSlots: { customRender: "status" }
      },
      {
        title: "",
        dataIndex: "operation",
        align: "center",
         width:'50px',
        scopedSlots: { customRender: "operation" }
      }
    ];

    return {
      routekey: ["photographRequest", "photographHistory;photographHistoryDetail"],
      loading: false,
      historyKey: "app/v1/target/extraction/header-photo",
      dataSource: [],
      columns: columns,
      pagination: { pageSize: 10 },  
      serviceApiId: ""
    };
  },
  components: {
    requestAndHisSubMenu
  },
  computed: {
    ...mapGetters(["apiHistoryList",'listPage'])
  },
  methods: {
    getImage(url) {
      let imageFront = "http://kotadata.oss-ap-southeast-5.aliyuncs.com";
      return imageFront + url;
    },
    getTime(time) {
      return this.$util.timestampToDay(time);
    },
    selectHistory(record) {
      this.$store.dispatch("changePage", this.pagination.current);
      this.$router.push({ name: "photographHistoryDetail", params: {record} });
    },
    // 获取getLog
    getLog(currentPage) {
      let params = {
        serviceApiId: this.serviceApiId,
        page:currentPage,
        pageSize: this.pageSize,
        showFullLoading: currentPage == 1 ? true : false //只要加载第一页的时候全局loading

      };
      console.log("getLog params", params);
      this.loading = true;
      this.$store.dispatch("getLog", params).then(
        res => {
          console.log("getLog:", res);
          this.dataSource = res.data.data;
          const pagination = { ...this.pagination };
          pagination.total = res.data.totalCount;
          this.loading = false;
          this.pagination = pagination;
        },
        error => {
          console.log("getLog error:", error);
        }
      );
    },
    getApiList() {
        console.log("getApiList start");
      this.$store.dispatch("getApiList", {}).then(
        res => {
           console.log("this.apiHistoryList:", this.apiHistoryList);
          let historyItem = this.apiHistoryList[this.historyKey];
          this.serviceApiId = historyItem._id;
          if(this.listPage){
              this.pagination.current=Number(this.listPage);
              this.getLog(this.listPage);            
          }else{
              this.getLog(1);
          }
        },
        error => {
          console.log("getLog error:", error);
        }
      );
    },
    handleTableChange(pagination, filters, sorter) {
      this.pagination.current = pagination.current;      
      this.getLog(pagination.current);
    }
  },
  created() {
    this.getApiList();
  }
};
</script>




